<template>
  <div class="map-view">
    <el-card>
      <div id="main"></div>
    </el-card>
  </div>
</template>

<script>
import {get,travel} from "@/api/api";
export default {
  data() {
    return {
    }
  },
  created(){
    travel().then(res=>{
      if(res.data.status===200){
        let {points,linesData}=res.data.data
        console.log(linesData)
        this.draw(points,linesData)
      }
    })
  },
  methods:{
    draw(points,linesData){
      {
        get('https://geo.datav.aliyun.com/areas_v3/bound/100000_full.json').then(res => {
          let getJson=res.data;
          let myChart = this.$echarts.init(document.getElementById('main'))
          this.$echarts.registerMap('china',getJson)  //注册可用的地图，必须包括geo组件或map图表类型的时候才可以
          console.log("getJson:",getJson);
          let option = {
            backgroundColor:'rgb(121,145,206)',
            geo: {
              map: 'china',
              aspectScale: 0.75, //scale地图长宽比
              zoom: 1.1,
              itemStyle:{ //坐标系
                normal:{
                  areaColor:{
                    type:'radial',
                    x:0.5,
                    y:0.5,
                    r:0.8,
                    colorStops:[
                      {
                        offset:0,
                        color:'#091312'   //0%处的颜色
                      },
                      {
                        offset:1,
                        color:'#274d68'  //100%处的颜色
                      }
                    ],
                    globalCoord:true
                  },
                  shadowColor:'rgb(58,115,192)',
                  shadowOffsetX:10,
                  shadowOffsetY:11
                }
              },
              regions:[{
                name:'南海诸岛',
                itemStyle: {
                  opacity:0.5
                }
              }]
            },
            series: [
              {//配置地图相关参数，绘制地图，这个对象是关于地图图表的相关设置
                //定义地图类型，数据
                type: 'map',
                label:{     //配置字体
                  normal:{
                    show:true,
                    textStyle:{
                      color:'#1DE9B6'   //字体颜色
                    }
                  },
                  emphasis:{
                    arguments:'rgb(183,185,145)',
                    borderWidth: 0.1,
                  }
                },
                zoom: 1.1,
                map: 'china',
                itemStyle:{
                  normal:{
                    backgroundColor:'rgb(147,235,248)', //背景颜色
                    borderWidth:1,    //边界线颜色
                    areaColor:{ //默认区域颜色
                      type:'radial',
                      x:0.5,
                      y:0.5,
                      r:0.8,
                      colorStops:[
                        {
                          offset:0,
                          color:'rgb(31,54,150)'   //0%处的颜色
                        },
                        {
                          offset:1,
                          color:'rgb(89,128,242)'  //100%处的颜色
                        }
                      ],
                      globalCoord:true
                    }
                  },
                  emphasis:{
                    areaColor:'rgb(46,229,206)',    //鼠标花过区域颜色
                    borderWidth: 0.1,
                  }
                },
              },
              //关于地图中散点图的配置
              {
                type:'effectScatter',
                coordinateSystem:'geo',
                showEffectOn:'render',
                symbolSize:10,  //散点大小
                zlevel:1,
                data:points,
                rippleEffect:{  //涟漪特效
                  peiod:15,
                  scale:4,
                  data:[{}],
                  brushType:'fill'  //波纹
                }
              },
                //关于地图中线路动画的设置
              {
                type: 'lines',
                zlevel: 2,
                effect:{
                  show:true,
                  period:4,         //周期
                  symbol:'arrow',   //箭头样式
                  symbolSize:7,     //箭头大小
                  trailLength:0.4,  //拖尾效果
                },
                lineStyle:{
                  normal:{
                    color:'#1DE986',
                    width:1,         //线的宽度
                    opacity: 0.1,
                    curveness:0.6,   //弯曲程度
                  }
                },
                data:linesData,
              }
            ]
          }
          myChart.setOption(option)
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.map-view{
  width: 100%;
  #main{
    width: 100%;
    height: 700px;
  }
}
</style>
